@import 'colors.scss'; //reset

.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

//app

//@import './reset'; //reset

//
// Mixins
//

@import './breakpoints';
@import './mixin';

html {
	font-size: 16px;

	@include size(lg) {
		font-size: 15px;
	}

	@include size(sm) {
		font-size: 14px;
	}

	@include size(xs) {
		font-size: 13px;
	}
}
ul {
	padding-left: 0;
}
li {
	list-style: none;
}
.content {
	display: flex;
	margin: 0 -2rem;

	@include size(lg) {
		flex-direction: column;
	}
	.list,
	.detail {
		padding: 0 2rem;
	}
}

.list {
	flex: 1 1 30%;
}

.detail {
	flex: 1 1 70%;
}

.app {
	width: 100%;
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: $makerBg;
}

* {
	box-sizing: border-box;
}
